import imgLogo from '@/assets/newHome/logo/Full_Logo_Horizontal_Transparent_Light.png';
import passwordLockImg from '@/assets/newHome/login/password-lock.png';
import { useRouter } from 'vue-router';
import { ref, reactive } from 'vue';
import { ElMessage } from 'element-plus';
import { SEND_EMAIL_CODE_REST_PASSWORD_V3 } from "@/api/api";
import { encode } from 'js-base64';
export default {
  name: "password",

  data() {
    return {
      imgLogo,
      passwordLockImg
    };
  },

  setup() {
    const router = useRouter();

    function turnHome() {
      return router.push('/');
    }

    function backToLogin() {
      return router.push('/login');
    }

    const passwordLoadingStatus = ref(false);
    const passwordForms = ref();
    const passwordForm = reactive({
      email: ''
    });
    const passwordRules = reactive({
      email: [{
        type: 'email',
        required: true,
        message: 'Enter a valid email address',
        trigger: 'blur'
      }]
    });

    function sendCode(formName) {
      passwordLoadingStatus.value = true;
      if (!formName) return;
      formName.validate(valid => {
        if (valid) {
          let params = Object.assign({}, passwordForm);
          SEND_EMAIL_CODE_REST_PASSWORD_V3(params).then(res => {
            console.log(res);

            if (res.code == 200) {
              passwordLoadingStatus.value = false;
              let paramsStr = encode(JSON.stringify(params));
              router.push({
                path: '/forgot/sendCode',
                query: {
                  str: encodeURIComponent(paramsStr)
                }
              });
            }
          }).catch(err => {
            console.log(err);
            passwordLoadingStatus.value = false;

            if (err.msg) {
              ElMessage({
                type: 'error',
                message: err.msg,
                grouping: true
              });
              return;
            }

            if (err.message) {
              ElMessage({
                type: 'error',
                message: err.message,
                grouping: true
              });
            }
          });
        } else {
          console.log('error submit!!');
          ElMessage({
            type: 'warning',
            message: 'Enter a valid email address',
            grouping: true
          });
          passwordLoadingStatus.value = false;
          return false;
        }
      });
    }

    return {
      sendCode,
      passwordForms,
      passwordForm,
      passwordRules,
      passwordLoadingStatus,
      turnHome,
      backToLogin
    };
  }

};